.pokedex-container{
    margin-left: 15%;
    display: flex;
    width: 70%;
    min-height: 475px;
    background-color:#f40552;
    flex-wrap: wrap;
    border-radius: 12px;
}

.pokelist-container{
    width: 300px;
    overflow-y: scroll;
    background-color: rgba( 252,126,47,0.5);
    height: 455px;
    margin: 10px;
    flex-grow: 3;
    border-radius: 10px 1px 1px 10px;
}

.pokesearchresult-container{
    display: flex;
    align-items: center;
    width: 300px;
    flex-grow: 1;
    margin: 10px;
    background-color: rgba( 252,126,47,0.5);
    height: 455px;
    border-radius: 1px 10px 10px;
}